<template>
  <div id="single-blog">
    <h1 v-rainbow>{{ blog.title }}</h1>
    <p>作者:{{ blog.author }}</p>
    <p>分类: <ul>
      <li v-for="(item, index) in blog.categories" :key="index" id="fenglei">
         {{item}}
      </li>
    </ul>
    </p>
<router-link :to="'/edit/'+id">编辑</router-link>
    <button @click="deleteSingBlog">删除</button>
    <article>{{ blog.content }}</article>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
    };
  },

  methods: {
    deleteSingBlog() {
      this.$http
        .delete(
          "https://burdemo-86cc9-default-rtdb.firebaseio.com/posts/" +
            this.id +
            ".json"
        )
        .then((response) => {
          this.$router.push({ path: "/" });
          response;
          console.log(this.$router.push);
        });
      alert("hello");
    },
  },

  created() {
    this.$http
      .get(
        "https://burdemo-86cc9-default-rtdb.firebaseio.com/posts/" +
          this.id +
          ".json"
      )
      .then((data) => {
        console.log(data);
        this.blog = data.body;
        //   console.log(this.blogs);
      });
  },
};
</script>

<style scoped>
#single-blog {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  background-color: rgb(255, 255, 255);
}

#fenglei {
  display: inline-block;
}
</style>