<template>
  <div id="add-blog">
    <h1>添加博客</h1>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label> 博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxs">
        <label>Vue.js</label>
        <input type="checkbox" value="1" v-model="blog.categories" />
        <label>Vue.js</label>
        <input type="checkbox" value="2" v-model="blog.categories" />
        <label>Vue.js</label>
        <input type="checkbox" value="3" v-model="blog.categories" />
        <label>Vue.js</label>
        <input type="checkbox" value="4" v-model="blog.categories" />
      </div>

      <label>作者:</label>
      <select v-model="blog.author">
        <option v-for="(item, index) in authors" :key="index">
          {{ item }}
        </option>
      </select>

      <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div>
      <h3 v-if="submmited">博客添加成功！</h3>
    </div>

    <hr />

    <div id="preview">
      <h3>博客总览</h3>

      <p>博客标题:{{ blog.title }}</p>

      <p>博客内容:</p>
      <p>{{ blog.content }}</p>

      <p>博客分类:
      <ul>
        <li v-for="(item, index) in blog.categories" :key="index">
          {{ item }}
        </li>
      </ul></p>

      <p>作者:{{ blog.author }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-blog",

  data() {
    return {
      blog: { title: "", content: "", categories: [], author: "" },
      authors: ["a", "b", "c"],
      submmited: false,
    };
  },

  methods: {
    post() {
      this.$http
        .post(
          "https://burdemo-86cc9-default-rtdb.firebaseio.com/posts.json",
          this.blog
        )
        .then(function (data) {
          console.log(data);
          this.submmited = true;
        });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

#checkboxs label {
  display: inline-block;
  margin-top: 0px;
}

#checkboxs input {
  display: inline-block;
  margin-right: 20px;
  border-radius: 50%;
}

input[type="text"],
textarea {
  display: block;
  width: 100%;
}

select {
  display: block;
  width: 30%;
}

button {
  display: block;
  margin: 20px auto;
  border: 0px solid black;
  padding: 14px;
  border-radius: 15px;
  font-size: 15px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px solid rgb(44, 43, 43);
  margin: 30px auto;
  background-color: aliceblue;
}

#preview h3 {
  margin-top: 10px;
}

#preview ul,
#preview ul li {
  display: inline-block;
}
</style>
