<template>
  <div v-theme="'wide'" id="show-blogs">
    <h1>博客总览</h1>

    <input type="text" v-model="search" placeholder="搜索" class="search" />

    <!-- <img src="./../../public/VR.png" alt=""> -->
    <div
      v-for="(blog, index) in filteredBlogs"
      :key="index"
      class="single-blog"
    >
      <router-link :to="'/blog/' + blog.id">
        <h2 v-rainbow>{{ blog.title | to-uppercase }}</h2>
        <!-- v-bind:value='rainbow'可缩写v-rainbow -->
        <article>{{ blog.content }}</article>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      search: "",
      blogs: [],
    };
  },

  created() {
    this.$http
      .get("https://burdemo-86cc9-default-rtdb.firebaseio.com/posts.json")
      .then((data) => {
        console.log(data);

        return data.json();
        // console.log(this.blogs);
      })
      .then(function (data) {
        var blogArray = [];
        for (let key in data) {
          data[key].id = key;
          blogArray.push(data[key]);
        }
        console.log(blogArray);
        this.blogs=blogArray
      });
  },

  computed: {
    filteredBlogs() {
      return this.blogs.filter((blogs) => {
        return blogs.title.match(this.search); //match（检索是否含有值）
      });
    },
  },

  filters: {
    toUppercase(value) {
      return value.toUpperCase();
    },
  },
};
</script>

<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  padding: 20px;
  margin: 20px auto;
  box-sizing: border-box;
  background-color: rgb(255, 255, 255);
  
}
 
a{text-decoration: none;}

.search {
  width: 400px;
}
</style>
